import { Grid, TextField } from "@mui/material";
import ImageField from "../../common/imageFeild/ImageField";
import NotificationImportantIcon from '@mui/icons-material/NotificationImportant';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { useSelector } from "react-redux/es/exports";
import { useDispatch } from "react-redux/es/exports";
import { useNavigate } from "react-router-dom";
import HomeIcon from '@mui/icons-material/Home';



const medic2 = require("../../images/flower.jpg")
const logo = require("../../images/medical.jpg")

export default function SignIn(props) {
    const user = useSelector((state) => state.feature.user);
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const medic1 = {
        src: medic2
    }
    const logo1 = {
        src: logo
    }

    const onClickHandler = () => {
        navigate("/pages/dashboard")
    }

    return (
        <>
            <Grid container spacing={2}>
                <Grid xs={12} md={4} lg={4} item>
                    <Grid container padding={5} spacing={5}>
                        <Grid style={{ paddingBottom: 40 }} xs={12} md={12} lg={12} item>
                            <ImageField image={logo1}></ImageField>
                        </Grid>

                        <Grid style={{ paddingTop: 50, marginLeft: 40 }} xs={12} md={12} lg={12} item>
                            <Typography sx={{ fontSize: 80 }}> <NotificationImportantIcon sx={{ width: 120, height: 120, color: 'pink' }} />404</Typography>
                        </Grid>
                        <Grid style={{ marginTop: 0 }} xs={12} md={12} lg={12} item>
                            <Typography sx={{ fontSize: 24, fontWeight: 600, marginLeft: 7 }}>Sorry! The page you were looking for doesn't exist.</Typography>
                        </Grid>
                        <Grid style={{ marginTop: 8, marginLeft: 80 }} xs={12} md={12} lg={12} item>
                            <Button variant="contained" onClick={onClickHandler}><HomeIcon />Back To Home</Button>
                        </Grid>

                    </Grid>
                </Grid>
                <Grid xs={0} md={8} lg={8} item >
                    <ImageField image={medic1}></ImageField>
                </Grid>
            </Grid>


        </>
    )
}