import * as React from 'react';

import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { Button, CardActionArea, CardActions } from '@mui/material';
import ImageField from '../imageFeild/ImageField';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import ButtonField from '../ButtonField/ButtonField';
import Box from '@mui/material/Box';




const doctor=require("../../images/Dr.Emma.jpg")
export default function DoctorCard3(props) {
const doctor1={
  src:doctor
}

  return (
   <>
   <Card sx={{ maxWidth: 345 ,alignItems:"center"}}>
   <CardMedia
        component="img"
        height="345"
        image={doctor1.src}
        alt="green iguana"
      />
     <Box sx={{paddingBottom:3,display:'flex',flexDirection:'column',alignItems:"center",justifyContent:"space-between"}}>
      <CardContent sx={{display:'flex',flexDirection:'column',alignItems:"center",justifyContent:"center"}}>
        <h2 >Dr. Emma</h2>
        <Typography sx={{paddingBottom:1}} variant="body2" color="grey" >
        Audiology
        </Typography>
        <div>
        <InstagramIcon sx={{padding:1}}></InstagramIcon>
        <FacebookIcon sx={{padding:1}}></FacebookIcon>
        <TwitterIcon sx={{padding:1}}></TwitterIcon>
        </div>
        
        <Typography variant="body2" color="grey"sx={{display:'flex',alignItems:'center',paddingLeft:3,flexDirection:'column'}} >795 Folsom Ave, Suite 600 San Francisco, CADGE 94107</Typography>
      </CardContent>
      <CardActions >
        <ButtonField styles={{fontWeight:600}}variant="contained" name="View Profile"></ButtonField>
      </CardActions>
      </Box>
    </Card>
   </>
  );
}