import { Grid, TextField } from "@mui/material";
import ImageField from "../../common/imageFeild/ImageField";
import Header from "../../common/Header/Header";
import MediaCard from "../../common/Card/MediaCard";
import MediaCard1 from "../../common/Card/MediaCard1";
import TableData from "../../common/TableData/TableData";
import ButtonField from "../../common/ButtonField/ButtonField";
import MediaCard2 from "../../common/Card/MediaCard2";
import MediaCard3 from "../../common/Card/MediaCard3";
import { Link } from "react-router-dom";
import Typography from "@mui/material/Typography";
import Footer from "../../Footer/Footer";



const Map = require("../../images/lineMap.png")
const PatientAge = require("../../images/patientAge.png")
const PatientGender = require("../../images/PatientGender.png")
const Department = require("../../images/Department.png")
const incomeMap = require("../../images/incomeMap.png")
const income = require("../../images/incom2.png")
const logo = require("../../images/medical.jpg")
export default function Dashboard() {

    const logo1 = {
        src: logo
    }
    const PatientAge1 = {
        src: PatientAge
    }
    const Department1 = {
        src: Department
    }
    const PatientGender1 = {
        src: PatientGender
    }
    const incomeMap1 = {
        src: incomeMap
    }
    const income1 = {
        src: income
    }
    const Map1 = {
        src: Map 
        }
    
    return (
    <>
        <Grid container spacing={2}>
            <Grid item xs={12} md={2} lg={2} >
                <Grid container padding={2} >
                    <Grid padding={1} xs={12} md={12} lg={12} item>
                        <ImageField image={logo1}></ImageField>
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                        <div><ButtonField variant={'disabled'}name={"MEDICINE"}></ButtonField></div>
                        <Link to={'/pages/dashboard'}style={{textDecoreation:"none"}}>
                          <Typography sx={{fontWeight:600,fonSize:12,color:'grey'}}> Dashboard</Typography> 
                            </Link>
                            <Link to={'/pages/Appointment'}style={{textDecoreation:"none"}}>
                          <Typography sx={{fontWeight:600,fonSize:14,color:'grey'}}> Appointment</Typography> 
                            </Link>
                            <Link to={'/pages/Doctors'}style={{textDecoreation:"none"}}>
                          <Typography sx={{fontWeight:600,fonSize:14,color:'grey'}}> Doctors</Typography> 
                            </Link>
                            <Link to={'/pages/Departments'}style={{textDecoreation:"none"}}>
                          <Typography sx={{fontWeight:600,fonSize:14,color:'grey'}}> Departments</Typography> 
                            </Link>
                            <Link to={'/pages/Patients'}style={{textDecoreation:"none"}}>
                          <Typography sx={{fontWeight:600,fonSize:14,color:'grey'}}>Patients</Typography> 
                            </Link>
                            <Link to={'/pages/Payments'}style={{textDecoreation:"none"}}>
                          <Typography sx={{fontWeight:600,fonSize:14,color:'grey'}}>Payments</Typography> 
                            </Link>
                       
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                        
                    <div><ButtonField variant={'disabled'}name={"UI KIT"}></ButtonField></div>
                    <div><Link to={'/pages/Components'}>Components</Link></div>
                    <div><Link to={'/pages/Icons'}>Icons</Link></div>
                    <div><Link to={'/pages/Typography'}>Typography</Link></div>
                    <div><Link to={'/pages/Tables'}>Tables</Link></div>
                    <div><Link to={'/pages/Forms'}>Forms</Link></div>
                    <div><Link to={'/pages/Charts'}>Charts</Link></div>
                    <div><Link to={'/pages/Maps'}>Maps</Link></div>
                  
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                    <div><ButtonField variant={'disabled'}name={"Apps"}></ButtonField></div>
                    </Grid>
                </Grid>
            </Grid>

            <Grid item xs={12} md={10} lg={10}>
                <Grid container padding={2} spacing={2}>
                    <Grid xs={12} md={12} lg={12} item>
                        <Header></Header>
                    </Grid>
                    <Grid sx={{ display: 'flex',marginTop:5 }} padding={2} xs={12} md={12} lg={12} item>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard></MediaCard>
                        </Grid>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard1></MediaCard1>
                        </Grid>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard2></MediaCard2>
                        </Grid>
                        <Grid padding={1} xs={12} md={3} lg={3} item>
                            <MediaCard3></MediaCard3>
                        </Grid>
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                        <div style={{ fontSize: 30 }}>Hospital Survey</div>
                        <ImageField image={Map1}></ImageField>
                    </Grid>
                    <Grid sx={{ display: 'flex' }} padding={2} xs={12} md={12} lg={12} item>
                        <Grid padding={2} xs={12} md={6} lg={6} item>
                            <div style={{ fontSize: 30 }}>$25038</div>
                            <span>Income in current month</span>
                            <ImageField image={incomeMap1}></ImageField>
                        </Grid>
                        <Grid padding={2} xs={12} md={6} lg={6} item>
                            <div style={{ fontSize: 30 }}>$2195</div>
                            <span>Income in current week</span>
                            <ImageField image={income1}></ImageField>
                        </Grid>
                    </Grid>
                    <Grid sx={{ display: 'flex' }} padding={2} xs={12} md={12} lg={12} item>
                        <Grid padding={2} xs={12} md={4} lg={4} item>
                            <div style={{ fontSize: 30 }}>Patients Age</div>
                            <ImageField image={PatientAge1}></ImageField>
                        </Grid>
                        <Grid padding={2} xs={12} md={4} lg={4} item>
                            <div style={{ fontSize: 30 }}>Patients Gender</div>
                            <ImageField image={PatientGender1}></ImageField>
                        </Grid>
                        <Grid padding={2} xs={12} md={4} lg={4} item>
                            <div style={{ fontSize: 30 }}>Department</div>
                            <ImageField image={Department1}></ImageField>
                        </Grid>
                    </Grid>
                    <Grid padding={2} xs={12} md={12} lg={12} item>
                        <div style={{ fontSize: 30 }}>Last Appointment</div>
                        <TableData></TableData>
                       
                    </Grid>
                    <Grid xs={12} md={12} lg={12} item>
                      <Footer to={'/pages/dashboard'} name={'Dashboard'}></Footer>
                    </Grid>
                </Grid>

            </Grid>
        </Grid>

    </>
    )
}