import * as React from 'react';
import Box from '@mui/material/Box';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import RestoreIcon from '@mui/icons-material/Restore';
import FavoriteIcon from '@mui/icons-material/Favorite';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { Link } from "react-router-dom";
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Typography from '@mui/material/Typography';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import SettingsSuggestIcon from '@mui/icons-material/SettingsSuggest';

export default function Footer(props) {

    return (
        <>
            <Box sx={{ right: 0, bottom: 0, position: 'fixed', width: 1105, borderTop: '1px solid #e9e9e9' }}>
                <BottomNavigation>
                    <Box sx={{ paddingTop: 2, marginLeft: 0 }}>
                        <Breadcrumbs
                            separator={<NavigateNextIcon fontSize="small" />}>
                            <Link key="1" color="inherit" to={'/pages/dashboard'}>
                                Dashboard
                            </Link>
                            <Typography key="3" color="text.primary" >
                                Default
                            </Typography>
                        </Breadcrumbs>
                    </Box>
                    <Box sx={{ paddingTop: 2, paddingRight: 1, marginLeft: 95}} >
                        <Typography>Version 1.4.0 </Typography>
                    </Box>
                    <Box sx={{ paddingTop: 2 }}>
                        <Link>
                            <SettingsSuggestIcon sx={{ color: 'blue' }} />
                        </Link>
                    </Box>
                </BottomNavigation>
            </Box>
        </>

    )
}