import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Badge from '@mui/material/Badge';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircle from '@mui/icons-material/AccountCircle';
import { Button } from '@mui/material';
import NotificationsIcon from '@mui/icons-material/Notifications';
import MoreIcon from '@mui/icons-material/MoreVert';
import Avatar from '@mui/material/Avatar';
import ListItemIcon from '@mui/material/ListItemIcon';
import PersonAdd from '@mui/icons-material/PersonAdd';
import Settings from '@mui/icons-material/Settings';
import Logout from '@mui/icons-material/Logout';
import EditIcon from '@mui/icons-material/Edit';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import FavoriteIcon from '@mui/icons-material/Favorite';
import { Link } from 'react-router-dom';
import GroupsIcon from '@mui/icons-material/Groups';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import MailIcon from '@mui/icons-material/Mail';
import MarkUnreadChatAltIcon from '@mui/icons-material/MarkUnreadChatAlt';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: 20,
  width:100,
  backgroundColor: '#ebebeb',
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
    border:'1px solid blue'
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width:380,
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  color:'grey',
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'grey',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
}));

export default function Header(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorE2, setAnchorE2] = React.useState(null);

    React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMenuOpen1 = Boolean(anchorE2);


  const handleProfileMenuOpen = (event: any) => {
    setAnchorEl(event.currentTarget);
  };
  const handleNotificationMenuOpen = (event: any) => {
    setAnchorE2(event.currentTarget);
  };

 

  const handleMenuClose = () => {
    setAnchorEl(null);
   
  };
  const handleMenuClose1 = () => {
    setAnchorE2(null);
 
  };

  

  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: 'down',
        horizontal: 'right',
      }}
      id={menuId}
      keepMounted
      transformOrigin={{
        vertical: 'down',
        horizontal: 'right',
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>
      <ListItemIcon>
      <EditIcon fontSize="small" />
    </ListItemIcon>Edit Account</MenuItem>
      <MenuItem onClick={handleMenuClose}> 
      <ListItemIcon>
      <PersonOutlineIcon fontSize="small" />
    </ListItemIcon>User Profile</MenuItem>
      <MenuItem onClick={handleMenuClose}>
    <ListItemIcon>
      <PersonAdd fontSize="small" />
    </ListItemIcon>
   Add another account
  </MenuItem>
  <MenuItem onClick={handleMenuClose}>
    <ListItemIcon>
      <Settings fontSize="small" />
    </ListItemIcon>
    Settings
  </MenuItem>
  <MenuItem onClick={handleMenuClose}>
    <ListItemIcon>
      <Logout fontSize="small" />
    </ListItemIcon>
    LogOut
  </MenuItem>
    </Menu>
 );

  const menuId1 = 'primary-search-account-menu';
  const renderMenu1 = (
    <Menu
      anchorEl={anchorE2}
      anchorOrigin={{
        vertical: 'down',
        horizontal: 'right',
      }}
      id={menuId1}
      keepMounted
      transformOrigin={{
        vertical: 'down',
        horizontal: 'right',
      }}
      open={isMenuOpen1}
      onClose={handleMenuClose1}
    >
      <MenuItem>
      <Typography sx={{fontWeight:600}}>Notification</Typography>
      <Link style={{fontSize:13,textDecoration:'none',color:'red',marginLeft:50}}to={'/pages/Error'}>Clear All</Link>
      </MenuItem>
      <MenuItem onClick={handleMenuClose1}>
      <ListItemIcon>
      <FavoriteIcon fontSize="small" />
    </ListItemIcon>
    <Link style={{fontSize:13,textDecoration:'none',color:'grey'}}to={'/pages/Error'}>Sara Crouch Like Your Photo</Link>
        </MenuItem>
        <MenuItem onClick={handleMenuClose1}>
      <ListItemIcon>
      <GroupsIcon fontSize="small" />
    </ListItemIcon>
    <Link style={{fontSize:13,textDecoration:'none',color:'grey'}}to={'/pages/Error'}>New User Register</Link>
        </MenuItem>
        <MenuItem onClick={handleMenuClose1}>
      <ListItemIcon>
      <AccountTreeIcon fontSize="small" />
    </ListItemIcon>
    <Link style={{fontSize:13,textDecoration:'none',color:'grey'}}to={'/pages/Error'}>Likhita shared your Post</Link>
        </MenuItem>
        <MenuItem onClick={handleMenuClose1}>
      <ListItemIcon>
      <MailIcon fontSize="small" />
    </ListItemIcon>
    <Link style={{fontSize:13,textDecoration:'none',color:'grey'}}to={'/pages/Error'}>You have a New Massage</Link>
        </MenuItem>
        <MenuItem onClick={handleMenuClose1}>
      
     <Button variant="contained" sx={{fontSize:12}}>View all Notification
<MarkUnreadChatAltIcon fontSize="small"/>
      </Button>
        </MenuItem>
      
    </Menu>

    
  );
 

  return (
    <Box  sx={{ flexGrow: 1}} >
    <AppBar position='fixed' sx={{width:1105,bgcolor:"white"}}>
        <Toolbar>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Type to Search"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
           
            <IconButton
              size="large"
              aria-label="show 5 new notifications"
              color="blue"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleNotificationMenuOpen}
            >
              <Badge badgeContent={5} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
             
             <Avatar alt="Karishma" src="/static/images/avatar/3.jpg" />
            </IconButton>
          </Box>
        </Toolbar>
        </AppBar>
     
      {renderMenu}
      {renderMenu1}
    </Box>
  );
}